'use strict';

var React = require('react');
var Map = require('./map/map.jsx');
var RouteHandler = require('react-router').RouteHandler;
var MetaActions = require('../actions/pageMetaActions');

module.exports  = React.createClass({displayName: "exports",
  render: function() {
    this.map = (
      React.createElement(Map, {
        scrollWheelZoom: false, 
        attributionControl: false, 
        className: "main-map"}, 
        React.createElement(RouteHandler, React.__spread({},  this.props))
      ));

    /* When the map is not visible (inactive tab, inactive slide, etc)
     * it should not be rendered -- Leaflet messes up the DOM manipulations
     * based on inaccurate div dimensions */
    if (this.state.leafletRenderable) {
      return this.map;
    } else {
      return React.createElement("div", {style: {'height': '1px'}});
    }
  },
  componentWillMount: function() {

    // var grantParent = React.findDOMNode(this) && React.findDOMNode(this).parentNode.parentNode;
    var parent = document.getElementById('asdb-map-app-embed');
    var _this = this;

    /* Anticipating the structure of
     * <div class="asdb-map" data-sector-id="Energy">
     *  <div class="asdb-map-app-embed">
     *    <this root element>
     *  </div>
     * </div>
     */

    if (parent && parent.getAttribute('data-country-id')) {
      var inputCountryId = parent.getAttribute('data-country-id');
      MetaActions.transitionTo('country', {}, {c: [inputCountryId]});

    } else if (parent && parent.getAttribute('data-sector-id')) {
      var inputSectorId = parent.getAttribute('data-sector-id');
      MetaActions.transitionTo('countrySector', {sectorId:inputSectorId},  {});

    } else if (parent && parent.getAttribute('data-project-id')) {
      var inputProjectId = parent.getAttribute('data-project-id');
      MetaActions.transitionTo('projectOnly', {projectId: inputProjectId}, {});

    }

    /* See note above about inactive tab interfering with Leaflet rendering*/
    var checkDivNotHidden = function() {
        if (parent.clientHeight > 0) {
          _this.setState({'leafletRenderable': true});
        } else {
          _this.setState({'leafletRenderable': false});
          window.setTimeout(checkDivNotHidden, 300);
        }
      };
    if (parent.clientHeight === 0) {
      console.warn('Map temporarily hidden because it is contained by an inactive element.');
      _this.setState({'leafletRenderable': false});
      window.setTimeout(checkDivNotHidden, 500);
    }
  }
});
